@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@1,200&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Caveat&display=swap");

#container {
  height: 100vh;
  width: 100vw;
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
}
nav {
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  letter-spacing: 3px;

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

nav a {
  text-decoration: none;
  transition: 0.3s ease;
  margin-right: 20px;
  color: white;
}
nav a:hover {
  text-decoration: underline;
}

main {
  height: 90vh;

  color: white;
  display: flex;
  flex-direction: column;

  justify-content: center;
}

.main {
  padding: 10px;
  display: flex;
  font-size: 3rem;
  font-family: cursive;
  align-items: center;
  justify-content: center;
}

.social {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.social p {
  margin-right: 10px;
}
.social a i {
  color: white;
}
.info {
  position: absolute;
  display: flex;
  flex-direction: column;
  bottom: 0;

  margin-bottom: 20px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  padding: 10px;
  letter-spacing: 3px;
}
.section {
  background-color: white;
  color: black;
  padding: 10px;
  margin-top: 20px;
}

@media (max-width: 768px) {
  .main {
    font-size: 2.5rem;
  }
  .social {
    font-size: 1.2rem;
  }
  nav p {
    font-size: 1.2rem;
  }
  p {
    font-size: 1.2rem;
  }
}
::-webkit-scrollbar {
  display: none;
}
